// 产生Ajax实例的  ,一般用axios

import Axios from 'axios';
import baseURL from './URLS';

const appkey = 'like_1633768593687';

// 创建一个请求实例
const request = Axios.create({
  baseURL,
  params: {
    appkey, // 默认把appkey传进去
  },
});

request.interceptors.response.use((resp) => {
  console.log(resp.data);
  return resp.data;
});
export default request;
