<template>
  <div id="app">
    <keep-alive>
    <router-view></router-view>
    </keep-alive>
  </div>
</template>
<script>

export default {
  created() {
    // 刚开始（app组件加载的时候），我就把本地存储的东西拿到，并存放到store。
    const counterMap = JSON.parse(window.localStorage.getItem('goods')) || {};
    console.log(counterMap);
    Object.keys(counterMap).forEach((key) => {
      if (counterMap[key] === 0) delete counterMap[key];
    });
    console.log(counterMap);
    this.$store.commit('setCounterMap', counterMap);
  },
};
</script>
