/*eslint-disable*/
import Vue from 'vue';
import VueRouter from 'vue-router';

import 'nprogress/nprogress.css';
import { start, done, configure } from 'nprogress';
import { date } from '@/utils';
import Layout from '@/views/Layout/index.vue';

// import Home from '@/views/Layout/Home/index.vue';

Vue.use(VueRouter); // 具名导入 以 使shaking  tree优化 起作用，减小第三方库nprogress打包到 chunk-vendors.js中的体积。

configure({

  trickleSpeed: 100, // 进度条的速度
  showSpinner: false, // 不要显示加载的小圈圈
});

// 辅助函数  (为了 调试 页面开始加载后的进度条效果 : 进度条 模拟页面加载过程 ， 实际由于prefetch的作用，
// 虽然还没有用到后续页面的js，但是浏览器在空闲的时候会把它们加载好，因此到时，
// 你切换页面的时候会很快， 但是，也有可能页面比较多， 可能会出现，js还没加载到，
// 我们就切换页面，加载组件配置对象，避免用户在那里傻等待（点击之后，页面没有动静）
// ，这时有进度条模拟指示页面加载过程，对用户来说，是很好的安慰剂)
async function getPageComponent(pageCompResolver) {
  if (process.env.NODE_ENV === 'production') {
    // console.log('组件开始加载，进度条开始');
    start();// 组件开始加载，进度条开始
    await date.delay(1500);
    done();// 组件加载结束 ，进度条结束
    // console.log('组件加载结束，进度条结束');
  }
  return pageCompResolver();
}

const routes = [
  {
    path: '/',
    redirect: '/home',
    component: Layout,
    children: [
      {
        path: 'home',
        name: 'Home',
        component: () => getPageComponent(() => import('@/views/Layout/Home/index.vue')),
      },
      {
        path: 'classify',
        name: 'Classify',
        component: () => getPageComponent(() => import('@/views/Layout/Classify/index.vue')),
        meta: {
          title: '分类',
          keepAlive: true,
          scrollTop: 0,
        },
      },
      {
        path: 'cart',
        name: 'Cart',
        component: () => getPageComponent(() => import('@/views/Layout/Cart/index.vue')),
      },
      {
        path: 'about',
        name: 'About',
        component: () => getPageComponent(() => import('@/views/Layout/About/index.vue')),

      },

    ],

  },
  {
    path: '/search',
    name: 'Search',
    component: () => getPageComponent(() => import('@/views/Search/index.vue')),
  },
  {
    path: '*',
    redirect: '/home',
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;

//借助 导航守卫 来记录页面切换前一刻的 滚动条位置 scrollTop
router.beforeEach((to, from, next) => {

    if (from.meta.keepAlive) {
      const $content = document.querySelector('.content'); // 列表的外层容器
      const scrollTop = $content ? $content.scrollTop : 0;
      // console.dir($content);
      // console.log('scrollTop',scrollTop,$content,$content.scrollTop);
      from.meta.scrollTop = scrollTop;
    }
  next();
});
