import Vue from 'vue';
import com from './index.vue';
// 扩展一个具有指定模板的子类Animate
const Animate = Vue.extend(com); // 衍生一个Vue构造函数的子类 ，子类的模板属性template固定配置为com

// 封装动画函数 ，这是一个全局动画函数 (先借助 组件构造函数 产生UI（组件实例连接的真实dom） ，
// 和  再利用 transition + 组件实例的数据响应式（改变数据 会 影响UI）  实现动画，来实现对展示逻辑的封装 ，
// 即实现对 动画的封装)

const animate = ({
  startX, startY, endX, endY, src, width, height,
}) => {
  const compInstance = new Animate({
    el: document.createElement('div'),
    data() {
      return {
        moveX: startX,
        moveY: startY,
        src,
        width,
        height,
        sx: 1,
        sy: 1,
        opacity: 1,
        exist: true,
      };
    },
  });
  document.body.appendChild(compInstance.$el);
  //   console.log(compInstance.$el);
  // js执行栈中代码执行完成之后（同步代码执行完成之后/主函数执行之后） ,执行下面代码，产生过渡动画。
  setTimeout(() => {
    compInstance.moveX = endX - (width / 2) * 0.1;
    compInstance.moveY = endY - (height / 2) * 0.1;
    compInstance.sx = 0.1;
    compInstance.sy = 0.1;
    compInstance.opacity = 0;
  }, 0);
  // 过渡动画结束之后，销毁组件实例 ，即销毁组件实例连接的真实dom  ， 即 销毁UI
  setTimeout(() => {
    // compInstance.$destroy();
    // compInstance.$el.remove();
    compInstance.exist = false;
  }, 1100);
};

export default animate;
