import Vue from 'vue';
import Vuex from 'vuex';
import { classify } from '@/api';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    sideList: [],
    goodList: [],
    size: 5, // 页容量一般是固定的
    loading: true,
    // loadingGoodList: true,
    // 商品类型
    type: null, // 下拉刷新时用,
    isFinished: true,
    counterMap: {},
  },
  getters: {
    badge(state) {
      const badge = Object.values(state.counterMap).reduce((prev, next) => prev + next, 0);
      if (badge > 99) return '99+';
      return badge;
    },
  },
  mutations: {
    setSideList(state, list) {
      state.sideList = list;
    },
    setLoading(state, bool) {
      state.loading = bool;
    },
    setGoodList(state, list) {
      state.goodList = [...state.goodList, ...list];// 每次请求的goodList数据，合并到一个数组中，方便处理 滚动加载
    },
    resetGoodList(state) {
      state.goodList = [];
    },
    setIsFinished(state, bool) {
      state.isFinished = bool;
    },
    // setLoadingGoodList(state, bool) {
    //   state.loadingGoodList = bool;
    // },
    setType(state, type) {
      state.type = type;
    },
    // 增减商品（设置 增减商品的 仓库存储（内存） ，以使得数据具有响应式 从而与视图建立联系 ， 同时也 方便在系统中操作该数据 ）
    setCounterMap(state, map) {
      state.counterMap = map;
    },
    // 操作增减商品的本地存储 goods 并 更新 counterMap
    changeCounterStorage(state, { id, value }) {
      if (state.counterMap[id]) {
        if (state.counterMap[id] === 1 && value === -1) {
          state.counterMap[id] = 0;
          // Vue.delete(state.counterMap[id]);
        } else {
          state.counterMap[id] += value;
          // Vue.set(state.counterMap,id,state.counterMap[id]+value);
        }
      } else {
        Vue.set(state.counterMap, id, value);
      }

      window.localStorage.setItem('goods', JSON.stringify(state.counterMap));
    },
  },
  actions: {
    async getSideList({ commit }, type) {
      commit('setLoading', true);
      const data = await classify.getSideBar(type);
      // if (typeof data[0] === 'number') data[0] = '全部';
      console.log(data[0]);
      commit('setSideList', data);
      commit('setLoading', false);
    },
    // 如果传递的参数比较多，就用一个对象来装我们要传递的数据 ,函数内部解构一下： 商品类型type， 页容量size，排序方式sort，第几页page
    async getGoodList({ state, commit }, options) {
      const { page, sort } = options;
      const type = options.type || state.type; // 意味着，你可以传type，也可以不传
      commit('setType', type);
      // commit('setLoadingGoodList', true);
      console.log('type', type, state.goodList);
      const { list, total } = await classify.getGoodList(type, page, state.size, sort);
      // commit('setLoadingGoodList', false);
      console.log(state.goodList.length, total);
      if (state.goodList.length < total) {
        commit('setGoodList', list);
        console.log(state.goodList);
        state.goodList.forEach((item) => {
          console.log(item.id);
        });

        console.log('true');

        return true;// 还没有搞完
      }
      console.log('false');

      return false;// 数据搞完了
    },
  },
  modules: {
  },
});
