import request from './request';
import { URLS } from './URLS';
// 接口 // 通过接口可以拿到一些数据

const getSideBar = (type) => request.get(URLS.getSideBar, {
  params: { type },
});
/**
 *
 * @param {商品类型 对应侧边栏数据 String} type
 * @param {翻页} page
 * @param {页容量 ，固定5} size
 * @param {商品列表中 商品排序类型} sort
 * @returns
 */
const getGoodList = (type, page, size, sort) => request.get(URLS.getGoodList, {
  params: {
    type, page, size, sort,
  },
});
export default {
  getSideBar,
  getGoodList,
};
